import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { environment } from 'src/environments/environment';
import { ClaimFormService } from 'src/services/claim-form/claim-form.service';
import { TranslateConfigService } from 'src/services/translate-config/translate-config.service';

@Component({
  selector: 'app-claim-form-review',
  templateUrl: './claim-form-review.page.html',
  styleUrls: ['./claim-form-review.page.scss'],
})
export class ClaimFormReviewPage implements OnInit {

  readonly assetUrl = environment.assetURL;
  formData: any;
  loaded: boolean;

  goBack() {
    this.navCtrl.navigateBack("/claim-form-doc", {
			replaceUrl: true,
			animated: true,
		});
  }

  /**
   * delete image based on the image data
   * @param imageToDelete 
   */
  async removeDocumentByImage(id: string) {
    const currentDocuments = this.claimFormService.getCurrentValue().documents;
    
    const updatedDocuments = currentDocuments.filter(doc => doc.id !== id);
    this.claimFormService.updateFormData({
      documents: updatedDocuments
    });

    this.formData = await this.claimFormService.getCurrentValue();
  }

  constructor(
    public navCtrl: NavController,
    public translate: TranslateService,
    public translateConfigService: TranslateConfigService,
    private claimFormService: ClaimFormService
  ) {
    this.translateConfigService.getDefaultLanguage();
  }

  ngOnInit() {
  }

  async ionViewWillEnter() {
    this.loaded = false;
    this.formData = this.claimFormService.getCurrentValue();
    console.log(JSON.stringify(this.formData));
    this.loaded = true;
  }
}
